:root {
  /* Background Colors */
  --frame-background: #0e0e0e;
  --button-background: #191919;
  --input-bg: color-mix(in srgb, #fff 20%, transparent);
  --input-bg-hover: color-mix(in srgb, #fff 40%, transparent);
  /* Accent Colors */
  --accent: #ff1f1f;
  --accent-hover: #ff5f5f;
  --accent-secondary: #ff0000;
  /* Text Colors */
  --c-white: #f5f5f5;
  --c-black: #000000;
  --c-secondary: #a0a0a0;
  /* Additional Colors */
  --gray-bg: #f8f8f8;
  --gray-bg-2: #d9d9d9;
  /* Border/Stroke Color */
  --stroke: #212121;
  --stroke-hover: #363636;
  --trans-02s: all 0.2s ease;
  --trans-03s: all 0.3s ease;
  --trans-05s: all 0.5s ease;
  --text-size-s: 14px;
  --text-size-m: 16px;
  --text-size-l: 18px;
  --text-size-xl: 20px;
  --text-size-xxl: 24px;
  /* Media Queries */
  --screen-sm: 640px;
  --screen-md: 768px;
  --screen-lg: 1024px;
  --screen-xl: 1280px;
  /* Section Padding */
  --sec-p-y: 80px;
  /* Gradients */
  --bg-gradient-1: linear-gradient(
    90deg,
    #ffe5fb 5%,
    #ff1c1c 51%,
    #ffe5c5 100%
  );
  --bg-gradient-2: linear-gradient(
    90deg,
    color-mix(in srgb, #ffe5fb, transparent 40%) 5%,
    color-mix(in srgb, #ff1c1c, transparent 40%) 51%,
    color-mix(in srgb, #ffe5c5, transparent 40%) 100%
  );
  --bg-gradient-border: linear-gradient(160deg, #fff 50%, #ffeded) padding-box ,
    linear-gradient(90deg, #ffe5fb 30%, #ff1c1c 60%, #ffe5c5) border-box;
  /* gradient border */
}

*,
*::before,
*::after {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  -webkit-tap-highlight-color: transparent;
}

html {
  scroll-behavior: smooth;
  scroll-padding-top: 100px;
}

body {
  font-family: "HelveticaNeue", sans-serif;
  font-size: 16px;
  background-color: #fefefe;
}

ul {
  list-style: none;
  padding: 0;
}

section {
  overflow: hidden;
}

img {
  max-width: 100%;
  display: block;
}

.container {
  width: 1152px;
  max-width: 95%;
  margin: 0 auto;
}

.container-2 {
  width: 986px;
  max-width: 90%;
  margin: 0 auto;
}

.sec-p-y {
  padding: var(--sec-p-y) 0;
}

.sec-p-y--top {
  padding-top: var(--sec-p-y);
}

.sec-p-y--bottom {
  padding-bottom: var(--sec-p-y);
}

.sec-p-x {
  padding: 0 var(--sec-p-y);
}

.btn,
.btn-accent-2,
.btn-accent {
  border: none;
  cursor: pointer;
  background-color: transparent;
  transition: var(--trans-02s);
  display: inline-block;
  padding: 13px 21px;
  border-radius: 16px;
  font-size: 16px;
  font-family: "HelveticaNeue", sans-serif;
}

.btn-reset {
  margin: 0;
  padding: 0;
  border: none;
  cursor: pointer;
  background-color: transparent;
}

.btn-accent {
  font-size: 24px;
  color: var(--c-white);
  background: linear-gradient(90deg, #ff4c4c 0%, #ff1c1c 100%);
}

.btn-accent:hover {
  transform: scale(1.03);
}

.btn-accent:active {
  transform: scale(0.98);
}

.btn-accent-2 {
  font-size: 24px;
  color: var(--c-white);
  background: linear-gradient(90deg, #ffe5fb 0%, #ff1c1c 61%, #ffe5c5 100%);
}

.btn-accent-2:hover {
  transform: scale(1.03);
}

.btn-accent-2:active {
  transform: scale(0.98);
}

.df {
  display: flex;
  align-items: center;
}

.df.col {
  flex-direction: column;
}

.df.col-rev {
  flex-direction: column-reverse;
}

.df.gap-5 {
  gap: 5px;
}

.df.gap-10 {
  gap: 10px;
}

.df.gap-15 {
  gap: 15px;
}

.df.gap-20 {
  gap: 20px;
}

.x-btn {
  --size: 36px;
  display: inline-grid;
  place-items: center;
  width: var(--size);
  height: var(--size);
  background: transparent;
  border: none;
  padding: 0;
  cursor: pointer;
  border-radius: 6px;
  position: absolute;
  top: 20px;
  right: 20px;
  color: #333;
}

.x-btn:hover {
  color: #000;
  background: rgba(0, 0, 0, 0.04);
}

.x-btn:focus {
  outline: 3px solid rgba(20, 120, 255, 0.2);
  outline-offset: 3px;
  border-radius: 8px;
}

.x-btn::before,
.x-btn::after {
  content: "";
  position: absolute;
  width: 60%;
  height: 2px;
  background: currentColor;
  left: 20%;
  top: 50%;
  transform-origin: center;
}

.x-btn::before {
  transform: translateY(-50%) rotate(45deg);
}

.x-btn::after {
  transform: translateY(-50%) rotate(-45deg);
}

.burger {
  --size: 28px;
  --bar-height: 2px;
  --bar-color: #333;
  position: relative;
  width: var(--size);
  height: var(--size);
  display: inline-flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 6px;
  background: none;
  border: none;
  cursor: pointer;
  padding: 0;
  transition: var(--trans-02s);
}

.burger span {
  display: block;
  width: 100%;
  height: var(--bar-height);
  background-color: var(--bar-color);
  border-radius: 4px;
}

.burger:hover {
  gap: 8px;
}

.d-none {
  display: none;
}

.d-block {
  display: block;
}

.text-c-accent {
  color: var(--accent);
}

.radial-bg-accent {
  background: radial-gradient(circle, #fcdbdb 50%, #fff, #ffffff 100%);
}

.img-cover img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.header {
  width: 100%;
  position: fixed;
  left: 0;
  top: 0;
  z-index: 100;
}

.header__content {
  width: 100%;
  background-color: var(--gray-bg);
  border-radius: 16px;
  margin-top: 18px;
  padding: 10px 56px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  text-align: center;
  box-shadow: 0px 0px 20px 2px rgba(255, 255, 255, 0.6);
}

.header__navbar {
  display: flex;
  align-items: center;
  justify-content: space-between;
  text-align: center;
  width: 100%;
}

.header__logo {
  font-size: 14px;
  font-weight: 600;
}

.header__logo svg {
  width: 21px;
  height: 21px;
}

.header__link,
.header__sign,
.header__logo {
  color: var(--c-black);
  font-size: 16px;
  text-decoration: none;
  transition: var(--trans-02s);
}

.header__link:hover,
.header__sign:hover,
.header__logo:hover {
  color: var(--accent);
}

.header__sign-icon {
  width: 24px;
  height: 24px;
  border-radius: 100%;
  background-color: var(--gray-bg-2);
}

.header-mobile {
  position: fixed;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  background-color: color-mix(in srgb, #000, transparent 50%);
  transition: opacity 0.3s ease-in-out, visibility 0.3s ease-in-out;
  opacity: 0;
  visibility: hidden;
  z-index: 100;
}

.header-mobile.active {
  opacity: 1;
  visibility: visible;
}

.header-mobile.active .header-mobile__content {
  top: 0;
}

.header-mobile__content {
  position: absolute;
  right: 0;
  top: -100px;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  padding: 60px 20px;
  height: auto;
  width: 100%;
  background-color: var(--gray-bg);
  border-radius: 0 0 16px 16px;
  transition: var(--trans-03s);
}

.header-mobile__navbar {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  gap: 20px;
}

footer {
  height: 350px;
  width: 100%;
  background: var(--accent);
}

.teachers-block {
  width: 100%;
  max-width: 218px;
  background: #fff;
  border-radius: 50px;
  box-shadow: -10px 25px 33px color-mix(in srgb, var(--accent), transparent 88%);
  transition: var(--trans-03s);
}

.teachers-block:hover {
  transform: scale(1.05);
}

.teachers-block:nth-child(1) {
  margin-top: 110px;
}

.teachers-block:nth-child(1) .teachers-block__img {
  transform: translate(-30px, 0px);
}

.teachers-block:nth-child(2) {
  margin-top: -20px;
}

.teachers-block:nth-child(2) .teachers-block__img {
  transform: translate(-50px, 0px) scale(1.15);
}

.teachers-block:nth-child(3) {
  margin-top: 76px;
}

.teachers-block:nth-child(3) .teachers-block__img {
  transform: translate(-5px, 0px);
}

.teachers-block:nth-child(4) .teachers-block__img {
  transform: translate(-30px, 0px) scale(1.3);
}

.teachers-block__img {
  height: 180px;
  margin-bottom: 5px;
  width: 125%;
  max-width: 270px;
  position: relative;
}

.teachers-block__img img {
  width: 100%;
  max-height: 280px;
  object-fit: contain;
  position: absolute;
  bottom: 0;
  right: 0;
}

.teachers-block__body {
  padding: 30px;
  padding-top: 0;
}

.teachers-block__title {
  font-size: 32px;
  margin-bottom: 8px;
}

.teachers-block__subtitle {
  font-size: 18px;
  color: var(--c-secondary);
  margin-bottom: 25px;
}

.teachers-block__audio-sub {
  font-size: 14px;
  color: var(--c-secondary);
  margin-left: 8px;
  margin-top: 8px;
}

.teachers-block__audio {
  display: flex;
  align-items: center;
  gap: 6px;
  background-color: #fff5f5;
  border-radius: 50px;
  padding: 10px 20px;
  width: fit-content;
  background-color: #ffeeee;
  border-radius: 12px;
  padding: 12px 14px;
  width: 100%;
  min-height: 54px;
}

.teachers-block__audio.active .teachers-block__audio-play .pause {
  display: block;
}

.teachers-block__audio.active .teachers-block__audio-play .play {
  display: none;
}

.teachers-block__audio.active .wave-bar {
  animation: wave 1.5s infinite ease-in-out;
}

.teachers-block__audio-play .pause {
  display: none;
}

.teachers-block .audio-wave {
  display: flex;
  align-items: center;
  gap: 2px;
}

.teachers-block .wave-bar {
  display: block;
  width: 4px;
  height: 20px;
  background-color: #ff9999;
  border-radius: 2px;
  transition: all 0.1s ease-in-out;
}

.teachers-block .wave-bar:nth-child(1) {
  animation-delay: 1s;
  height: 9px;
}

.teachers-block .wave-bar:nth-child(2) {
  animation-delay: 1.7s;
  height: 23px;
}

.teachers-block .wave-bar:nth-child(3) {
  animation-delay: 1.4s;
  height: 26px;
}

.teachers-block .wave-bar:nth-child(4) {
  animation-delay: 2.5s;
  height: 24px;
}

.teachers-block .wave-bar:nth-child(5) {
  animation-delay: 1s;
  height: 14px;
}

.teachers-block .wave-bar:nth-child(6) {
  animation-delay: 1.2s;
  height: 20px;
}

.teachers-block .wave-bar:nth-child(7) {
  animation-delay: 0.1s;
  height: 8px;
}

.teachers-block .wave-bar:nth-child(8) {
  animation-delay: 2.4s;
  height: 24px;
}

.teachers-block .wave-bar:nth-child(9) {
  animation-delay: 0.9s;
  height: 23px;
}

.teachers-block .wave-bar:nth-child(10) {
  animation-delay: 1.8s;
  height: 12px;
}

.teachers-block .wave-bar:nth-child(11) {
  animation-delay: 1.9s;
  height: 6px;
}

.teachers-block .wave-bar:nth-child(12) {
  animation-delay: 1.5s;
  height: 13px;
}

.teachers-block .wave-bar:nth-child(13) {
  animation-delay: 1.3s;
  height: 25px;
}

.teachers-block .wave-bar:nth-child(14) {
  animation-delay: 1.6s;
  height: 6px;
}

.teachers-block .wave-bar:nth-child(15) {
  animation-delay: 1.7s;
  height: 26px;
}

.teachers-block .wave-bar:nth-child(16) {
  animation-delay: 1.4s;
  height: 19px;
}

.teachers-block .wave-bar:nth-child(17) {
  animation-delay: 1.5s;
  height: 20px;
}

@keyframes wave {
  0%, 100% {
    height: 10px;
  }

  50% {
    height: 30px;
  }
}

.sec-banner {
  --pad-top: 90px;
  padding-top: var(--pad-top);
  min-height: 100svh;
  background: linear-gradient(160deg, transparent 60%, color-mix(in srgb, var(--accent), transparent 75%), color-mix(in srgb, var(--accent), transparent 45%));
}

.sec-banner__content {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.sec-banner__img {
  max-width: 90%;
  margin: 0 auto;
}

.sec-banner .container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  z-index: 1;
  position: relative;
}

.sec-banner__text {
  margin-top: -60px;
  text-align: center;
  padding: 36px 50px;
  border-radius: 97px;
  position: relative;
  box-shadow: 0px 1px 20px color-mix(in srgb, var(--accent-secondary), transparent 75%);
  border: 4px solid transparent;
  background: linear-gradient(160deg, #fff 50%, #ffeded) padding-box, linear-gradient(90deg, #ffe5fb 30%, #ff1c1c 60%, #ffe5c5) border-box;
  /* gradient border */
  animation-name: banner__text_bg;
  animation-duration: 5s;
  animation-timing-function: ease-in-out;
  animation-iteration-count: infinite;
  animation-direction: alternate;
}

.sec-banner__text h3 {
  font-size: 49px;
  font-weight: 400;
}

.sec-banner__text span {
  font-size: 24px;
  letter-spacing: 0.5px;
  font-weight: 300;
}

.sec-banner__preimg {
  width: 350px;
  max-width: 100%;
  position: absolute;
  left: -50px;
  bottom: -50px;
}

.sec-banner__btn {
  margin-top: -25px;
  z-index: 5;
}

@keyframes banner__text_bg {
  0% {
    background-size: 100%;
  }

  50% {
    background-size: 300%;
    background-position: right;
  }

  100% {
    background-size: 100%;
  }
}

.sec-teachers__title {
  font-size: 260px;
  text-align: center;
  font-weight: 600;
  background: linear-gradient(90deg, #ffe5fb 0%, #ffb8b8 60%, #ffe5c5 100%);
  -webkit-background-clip: text;
  /* обрезаем фон по тексту */
  -webkit-text-fill-color: transparent;
  /* делаем сам текст прозрачным */
}

.sec-teachers__list {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  align-items: start;
}

.sec-generate {
