}

.sec-ftest__title {
  text-align: center;
  font-size: 216px;
  font-weight: 600;
  color: #fff;
}

.sec-ftest__content {
  margin-top: -80px;
  display: grid;
  grid-template-columns: 48% 1fr;
  gap: 5px;
}

.sec-ftest__text {
  font-size: 56px;
}

.sec-ftest__subtext {
  margin-top: 8px;
  font-size: 18px;
  margin-bottom: 12px;
}

.sec-ftest__desc {
  position: relative;
  padding-left: 50px;
}

.sec-ftest__desc:before {
  content: "";
  width: 34px;
  height: 100%;
  display: block;
  background: url("../img/ftest-arrow-down.svg") center/contain no-repeat;
  position: absolute;
  left: 0;
  top: 0%;
}

.sec-ftest__img {
  max-height: 300px;
  overflow: hidden;
  border-radius: 38px;
  box-shadow: -15px 8px 36px color-mix(in srgb, var(--accent-secondary), transparent 66%);
}

.sec-ftest__img img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.sec-ftest__btn {
  font-size: 18px;
}

.sec-tarifs {
  padding: 40px 0;
  padding-bottom: 60px;
}

.sec-tarifs__title {
  font-size: 38px;
  margin-bottom: 60px;
  text-align: center;
}

.sec-tarifs__list {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  gap: 10px;
}

.sec-tarifs__item {
  padding: 12px 14px 8px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  color: #000;
  background-color: #fff;
  border-radius: 18px;
  transition: all 0.3s ease, color 0.1s ease;
  position: relative;
  box-shadow: 0px 12px 148px color-mix(in srgb, var(--accent-secondary), transparent 66%);
}

.sec-tarifs__item:hover {
  transform: translateY(-35px);
  background: var(--accent);
  color: #fff;
}

.sec-tarifs__item::before {
  content: "";
  width: 90%;
  height: 90%;
  background: center/contain no-repeat url("../img/tarifs-bg-radial.png");
  background-position: 0px 40px;
  position: absolute;
  left: 0;
  top: 0;
}

.sec-tarifs__item.talking .sec-tarifs__item-tags p {
  background-color: #ff74a5;
  color: #fff;
}

.sec-tarifs__item.talking .sec-tarifs__item-tags span {
  background-color: #ffeffa;
  color: #ffa3c8;
